/* jshint ignore:start */

define('libertyjs-dev/config/environment', ['ember'], function(Ember) {
  var prefix = 'libertyjs-dev';
/* jshint ignore:start */

try {
  var metaName = prefix + '/config/environment';
  var rawConfig = Ember['default'].$('meta[name="' + metaName + '"]').attr('content');
  var config = JSON.parse(unescape(rawConfig));

  return { 'default': config };
}
catch(err) {
  throw new Error('Could not read config from meta tag with name "' + metaName + '".');
}

/* jshint ignore:end */

});

if (runningTests) {
  require("libertyjs-dev/tests/test-helper");
} else {
  require("libertyjs-dev/app")["default"].create({"name":"libertyjs-dev","version":"0.0.0.cbde7fee"});
}

/* jshint ignore:end */
